* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

ul,
ol,
li {
  list-style: none;
}

a {
  color: initial;
  text-decoration: none;
}

fieldset {
  border: none;
}

table {
  border: none;
  border-collapse: collapse;

  th,
  td {
    border: none;
  }
}

button {
  border: none;
  cursor: pointer;
}

img {
  max-width: 100%;
  vertical-align: middle;
}